.contacts {
    background-color: var(--main-background-color);
    padding-bottom: 39px;

    font-size: 12px;
    letter-spacing: 0.04em;
    font-weight: 500;

    position: relative;

    @include tablet {
        font-size: 14px;

    }

    @include desktop {
        font-size: 16px;


    }



    &__container {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
}

.address-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 32px;

    @include tablet {
        flex-direction: row;
        gap: 16px;
        margin-bottom: 60px;

    }

    @include desktop {
        gap: 32px;
        margin-bottom: 64px;

    }


    &__item {
        width: 280px;
        background: #fff;
        box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
        border-radius: 24px;
        padding: 40px 32px;

        @include tablet {
            width: 224px;
            padding: 40px 24px;
        }

        @include desktop {
            width: 368px;
            padding: 40px 40px;
        }
    }


    &__food-store {
        color: var(--primary-text-color);
        background-color: var(--hover-text-color);
        display: inline-block;
        border-radius: 4px;
        padding: 0 23px;
        margin-bottom: 20px;

        text-transform: uppercase;
        line-height: 1.86;
        text-align: center;
        letter-spacing: 0.04em;

        @include tablet {
            margin-bottom: 32px;
        }

        @include desktop {
            margin-bottom: 32px;

        }

    }
}

.discription-list {
    &__item {
        border-bottom: 1px solid #E1E1E1;
        padding-bottom: 24px;
        padding-top: 24px;
    }

    @include tablet {
        padding-bottom: 32px;
        padding-top: 32px;
    }

}

.discription-list__item:first-child {
    padding-top: 0;
}

.discription-list__item:last-child {
    padding-bottom: 0;
    border: none;
}

.phis-address {
    color: var(--primary-text-color);
    margin-bottom: 8px;
    width: 100%;

    line-height: 186%;
    font-weight: 500;
    letter-spacing: 0.04em;

    @include tablet {
        margin-bottom: 12px;
    }

    @include desktop {
        margin-bottom: 8px;
    }

    &__link {
        display: inline-block;

        line-height: 1.83;
        letter-spacing: 0.04em;
        text-decoration: none;

        color: #D6936D;
    }

    &__link:hover,
    &__link:focus {
        text-decoration: underline;
    }


}

.schedule-list {
    &__item:first-child {
        margin-bottom: 8px;

    }

    &__days,
    &__hours {
        color: var(--primary-text-color);
        line-height: 1.86;
    }

    &__hours {
        color: var(--primary-inactive-color);
    }
}

.contacts-list__link {
    color: var(--primary-text-color);
    text-decoration: none;
    line-height: 1.5;
}

.contacts-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 16px;

    @include tablet {
        flex-direction: row;
    }

    @include desktop {
        flex-direction: row;
    }
}

.contact-button__svg {
    margin-left: 6px;
}
.contacts-button {
    display: flex;
    align-items: center;
    text-align: center;

    background-color: var(--hover-text-color);
    border: none;
    padding: 12px 34px;
    outline: none;

    font-weight: 600;
    border-radius: 10px;
    font-size: 14px;
    line-height: 1.29;

    transition: color 250ms var(--cubic),
        border-color 250ms var(--cubic),
        fill 250ms var(--cubic);

    &:hover {
        background-color: var(--primary-button-color);
    }

    &:focus {
        background-color: var(--primary-button-color);
        color: var(--main-text-color);
        fill: var(--main-text-color);
    }

    &:disabled {
        color: var(--primary-text-color);
        background-color: var(--primary-inactive-color);
    }
}

// кнопка з пустим фоном
.contacts-button--secondary {
    display: flex;
    align-items: center;
    text-align: center;

    background-color: transparent;
    border: none;
    border-radius: 10px;
    padding: 12px 46px;
    border: 2px solid var(--hover-text-color);
    border-radius: 10px;
    outline: none;

    font-weight: 600;
    font-size: 14px;
    line-height: 1.29;

    transition: color 250ms var(--cubic),
        border-color 250ms var(--cubic),
        fill 250ms var(--cubic);

    &:hover {
        border-color: var(--primary-button-color);

    }

    &:focus {
        color: var(--main-text-color);
        border-color: var(--primary-button-color);
        fill: var(--main-text-color);
    }

    &:disabled {
        border-color: var(--primary-inactive-color);
        color: var(--primary-text-color);
    }
}